@font-face {
    font-family: "FiraCode";
    src: url("../fonts/FiraCode/FiraCode-Medium.ttf"); /* IE9 Compat Modes */
    src: url("../fonts/FiraCode/FiraCode-Medium.ttf") format("truetype"); /* Safari, Android, iOS */
}
@font-face {
    font-family: "Hackman-Bold";
    src: url("../fonts/FiraCode/FiraCode-Bold.ttf"); /* IE9 Compat Modes */
    src: url("../fonts/FiraCode/FiraCode-Bold.ttf") format("truetype"); /* Safari, Android, iOS */
}
*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;

    outline: 0 none !important;
}
html {
    box-sizing: border-box;
    font-size: 62.5%; /* 10px / 16px -> 62.5% -> 1rem -> 10px */
}
html,
body,
#react_root {
    position: relative;

    font-family: "Hackman" !important;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;

    width: 100vw;
    height: 100vh;

    top: 0px;
    left: 0px;
}
h2.article-title {
    font: 400 4rem "Pacifico", Helvetica, sans-serif;
    color: #505763;
    text-shadow: 3px 3px 0px rgba(0, 0, 0, 0.1);
}
.xterm-helpers {
    height: 0px;
    overflow: hidden;
}
